import { useEffect } from "react";
import gsap from "gsap";
import { isMobile } from "react-device-detect";
import ScrollToPlugin from "gsap/ScrollToPlugin";
import ScrollTrigger from '@terwanerik/scrolltrigger';
import Navigation from "./Navigation";

function Header(props) {
	let data=props.propsValue;

	useEffect(() => {
		gsap.registerPlugin(ScrollToPlugin);

		let tabBtn=document.getElementById("tab");
		let exitBtn=document.getElementById("exit");
		let dim=document.querySelector(".dim");
		let gnb=document.getElementById("gnb");
		let gnbLi=gnb.firstElementChild.children;
		let page=document.querySelectorAll("section");
		let pageList=[];
		pageList[0]=document.getElementById("main");
		let mobile=document.getElementById("mobile");
		let mobileLi=mobile.firstElementChild.children;
		let headerFix=document.querySelector("header");
		let footer=document.getElementById("footer");
		let topBtn=document.getElementById("arrow");
		let scroll=document.querySelector(".scroll");
		let video=document.getElementById("main_video");
	
		for(let i=0; i<page.length; i++){
			pageList.push(page[i]);
		}
	
		let n=0;
		let top=0;
		let winh;
	
		/* tab, exit, dim */

		tabBtn.addEventListener("click", function(e){
			e.preventDefault();	
			document.body.classList.toggle("fixed");
			mobile.classList.toggle("active");
			dim.classList.toggle("active");
			exitBtn.classList.toggle("active");
		});
	
		exitBtn.addEventListener("click", function(e){
			e.preventDefault();
	
			mobile.classList.remove("active");
			dim.classList.remove("active");
			document.body.classList.remove("fixed");
		});
	
		dim.addEventListener("click", function(){
			mobile.classList.remove("active");
			dim.classList.remove("active");
			document.body.classList.add("fixed");
		});
	

		
		/* video */
		video.setAttribute("src", process.env.PUBLIC_URL + "/assets/images/main_bg.mp4");

		video.addEventListener("loadeddata", function(){
			video.muted=true;
			video.play();
		});
	
		video.addEventListener("ended", function(){
			video.play();
		});
	
	
		/* scrolltrigger */
		const trigger=new ScrollTrigger({ 
			trigger: {
				once: true,
				toggle: {
					class: {
						in: "active",
						out: "inactive"
					}
				},
				offset: {
					viewport: {
						y: 0.25
					}
				}
			},
			scroll: {
				callback: offset => scrollInteraction(offset.y)
			}
		});
	
	
		trigger.add("section");
	
	
		function scrollInteraction(t){
			if(t < pageList[1].offsetTop){
				n=0;
			}
			else if(t < pageList[2].offsetTop){
				n=1;
			}
			else if(t < pageList[3].offsetTop){
				n=2;
			}
			else if(t < pageList[4].offsetTop){
				n=3;
			}
			else if(t < pageList[5].offsetTop){
				n=4;

				if(window.innerHeight + t === document.body.scrollHeight){
					n=5;
				}
			}
			else {
				n=5;
			}
			
			for(let i=1; i<gnbLi.length; i++){
				if(i === n){
					if(gnbLi[i-1].classList.contains("active") === false){
						gnbLi[i-1].classList.add("active");
					}
				}
				else{
					if(gnbLi[i-1].classList.contains("active") === true){
						gnbLi[i-1].classList.remove("active");
					}
				
				}
			}
	
			if(t > 70){
				if(headerFix.classList.contains("fixed") === false){
					headerFix.classList.add("fixed");
					tabBtn.classList.add("active");
					topBtn.classList.add("active");
					
					gsap.fromTo(headerFix, {top: -70}, {top: 0,  delay: 0.5});
				}
	
			}
			else{
				if(headerFix.classList.contains("fixed") === true){
					headerFix.classList.remove("fixed");
					tabBtn.classList.remove("active");
					topBtn.classList.remove("active");
	
					topBtn.removeAttribute("style");
					headerFix.removeAttribute("style");
					tabBtn.removeAttribute("style");
				}
			}
		} 


		function myFn(f){
			top=gnbLi[f+1].firstElementChild.getAttribute("href");
			top=pageList[f+1].offsetTop;
	
			gsap.to(window, {scrollTo : top, duration: 0.4, delay: 0.4});
		}
	
		for(let i=0; i<gnbLi.length; i++){
			gnbLi[i].addEventListener("click", function(e){
				e.preventDefault();

				if(i === 5) return;
	
				myFn(i);
			});
		}
	
	
		for(let i=0; i<mobileLi.length; i++){
			mobileLi[i].addEventListener("click", function(e){
				e.preventDefault();
	
				if(i === 5) return;

				dimTrigger();
				myFn(i);
			});
		}

		dim.addEventListener("click", function(){
			dimTrigger();
		});
	
		function dimTrigger(){
			document.body.classList.remove("fixed");
			mobile.classList.remove("active");
			dim.classList.remove("active");
		}
	
	
	
	
		/* footer banner */
		let bannerw=150;
		let sign=footer.firstElementChild; // .signature
		let inner, moveUl, direction, prev, next;
	
		for(let item of sign.children){
			if(item.className === "inner"){
				inner=item;
				moveUl=inner.firstElementChild;
			}
			else if(item.className === "direction"){
				direction=item;
			}
		}
	
	
		for(let item of direction.children){
			if(item.className === "prev"){
				prev=item;
			}
			else if(item.className === "next"){
				next=item;
			}
		}

		/* resize */
	
		function resizeHandler(){
			winh=window.innerHeight;
			if(isMobile){
				if(window.innerWidth > winh){ // landscape
					scroll.style.display="none";
				}
				else{
					scroll.style.display="block";
				}
			}
	
			bannerw=inner.offsetWidth/5;
	
			for(let item of moveUl.children){
				item.style.width=bannerw+"px";
			}

	
			if(winh < 720){
				if(mobile.classList.contains("active") === true){
					dimTrigger();
				}
			}

		}
	
		resizeHandler();
	
		window.addEventListener("resize", resizeHandler);
	
		prev.addEventListener("click", function(e){
			e.preventDefault();
	
			rightMoving(moveUl);
		});
		next.addEventListener("click", function(e){
			e.preventDefault();
			
			leftMoving(moveUl);
		});
	
		function leftMoving(cont){
			let targetx=0;
			targetx-=bannerw;
	
			gsap.to(cont, {left: targetx, duration: 0.4, onComplete: function(){
				let firstChild=cont.firstElementChild;
				let clone=firstChild.cloneNode(true);
				cont.removeChild(firstChild);
				cont.appendChild(clone);
				targetx+=bannerw;
				cont.style.left=targetx+"px";
			}});
		}
	
		function rightMoving(cont){
			let firstChild=cont.firstElementChild;
			let lastChild=cont.lastElementChild;
			let clone=lastChild.cloneNode(true);
			cont.removeChild(lastChild);
			cont.insertBefore(clone, firstChild);
	
			let targetx=0;
			targetx-=bannerw;
			cont.style.left=targetx+"px";
			targetx+=bannerw;
			gsap.to(cont, {left: targetx, duration: 0.4});
		}

		topBtn.addEventListener("click", function(e){
			e.preventDefault();
			
			gsap.to(window, {scrollTo:0, duration: 0.4});
		});
	});

	return(
		<header>
			<div className="inner">
				<h1><a href="">logo</a></h1>
				<Navigation propsValue={data}/>
				<a href="" id="tab">
					<span className="tab_line top"></span>
					<span className="tab_line middle"></span>
					<span className="tab_line bottom"></span>
				</a>
			</div>
		</header>
	);
}

export default Header;