function CultureList(props){
	let {img, strong, span}=props.propsValue;

	return(
		<li>
			<a href="">
				<div className={img}></div>
				<div className="desc">
					<strong>{strong}</strong>
					<p>
						{
							span.map((d, i) => <span key={i}>{d}</span>)
						}
					</p>
				</div>
			</a>
		</li>
	);
}

export default CultureList;