import Title from "./Title";
import MediaList from "./MediaList";
import { useSelector } from "react-redux";

function Media(){
	let title=useSelector(state => state.title);
	let media=useSelector(state => state.media);

	return(
		<section id="page2">
			<div className="inner">
				<Title propsValue={title.media}/>
				<div className="content">
					<ul>
						{
							media.map((d, i) => <MediaList key={i} propsValue={d}/>)
						}
					</ul>
				</div>
			</div>
		</section>
	);
}

export default Media;