import Title from "./Title";
import CultureList from "./CultureList";
import { useSelector } from "react-redux";

function Culture(){
	let title=useSelector(state => state.title);
	let culture=useSelector(state => state.culture);

	return(
		<section id="page3">
			<Title propsValue={title.culture}/>
			<div className="content">
				<ul>
					{
						culture.map((d, i) => <CultureList key={i} propsValue={d} />)
					}
				</ul>
			</div>
		</section>
	);
}

export default Culture;