function MainVideo(props){
	let {strong, span, scroll}=props.propsValue;
	return(
		<div className="main_bg">
			<video id="main_video" alt="video" muted autoPlay playsInline poster={process.env.PUBLIC_URL + "/assets/images/video_poster.jpg"}></video>
			<div className="desc">
				<h3><strong>{strong}</strong></h3>
				<p>
					{
						span.map((d, i) => <span key={i}>{d}</span>)
					}
				</p>
			</div>
			<div className="scroll">
				<strong>{scroll}</strong>
				<span></span>
			</div>
		</div>
	);
}

export default MainVideo;