import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation } from 'swiper/modules';
import { useSelector } from 'react-redux';
import 'swiper/css/navigation';
import 'swiper/css';

function Game(){
	let game=useSelector(state => state.game);
	return(
		<section id="page4">
			<div id="main_slider">
				<Swiper 
				loop={true}
				modules={[Navigation]}
				navigation
				className="swiper-container mainSwiper">
					{
						game.map((d, i) => 					
						<SwiperSlide key={i}>
							<img src={process.env.PUBLIC_URL + "/assets/images/" + d.main.path} alt={d.main.alt} />
							{(d.mobileLogo) ? <div className="m_phone_logo"><img src={process.env.PUBLIC_URL + "/assets/images/" + d.mobileLogo.path}  alt={d.mobileLogo.alt}/></div> : ""} 
							{(d.floatLogo) ? <div className="img_wrap desk"><img src={process.env.PUBLIC_URL + "/assets/images/" + d.floatLogo.path} alt={d.floatLogo.alt} /></div> : ""}
							{(d.char,d.phone) ? 
								<div className="img_wrap m_phone">
									<div className="char">
										<img src={process.env.PUBLIC_URL + "/assets/images/" + d.char.path} alt={d.char.alt} />
									</div>
									<div className="phone"> 
									<img src={process.env.PUBLIC_URL + "/assets/images/" + d.phone.path} alt={d.phone.alt} />
									</div>
							</div> : ""}
						</SwiperSlide>
						)
					} 
				</Swiper>
			</div>
		</section>
	);
}

export default Game;