import Header from "./Header";
import MainVideo from "./MainVideo";
import Mobile from "./Mobile";
import { useSelector } from "react-redux";

function Main(){
	let navigation=useSelector(state => state.navigation);
	let maintext=useSelector(state => state.maintext);

	return(
		<div id="main">
			<Header propsValue={navigation}/>
			<Mobile propsValue={navigation}/>
			<MainVideo propsValue={maintext}/>
		</div>
	);
}

export default Main;