function MediaList(props){
	let {path, alt, strong, span}=props.propsValue;
	return(
		<li>
		<a href="">
			<div className="img_wrap">
				<img src={process.env.PUBLIC_URL + "/assets/images/" + path} alt={alt} />
			</div>
			<div className="desc">
				<strong>{strong}</strong>
				<span>{span}</span>
			</div>
		</a>
	</li>
	);
}

export default MediaList;