import Signature from "./Signature";
import Sns from "./Sns";
import { useSelector } from "react-redux";



function Footer(){
	let footer=useSelector(state => state.footer);

	return(
		<footer id="footer">
			<div className="signature">
				<div className="inner">
					<ul>
						{ footer.signature.map((d, i) => <Signature key={i} propsValue={d}/>)}
					</ul>
				</div>
				<div className="direction">
					<a href="" className="prev"><img src={process.env.PUBLIC_URL + "/assets/images/left-arrow_white.png"} alt="left_arrow_white" /></a>
					<a href="" className="next"><img src={process.env.PUBLIC_URL + "/assets/images/right-arrow_white.png"} alt="right_arrow_white" /></a>
				</div>
			</div>
			<div className="content">
				<div className="address">
					<a href="" className="logo"><img src={process.env.PUBLIC_URL + "/assets/images/logo_black.svg"} alt="logo_black" /></a>
					<p className="copyright">© Pearl Abyss Corp. All Rights Reserved.</p>
				</div>
				<div className="sns">
					<ul>
						{
							footer.sns.map((d, i) => <Sns key={i} propsValue={d}/>)
						}
					</ul>
				</div>
			</div>
		</footer>
	);
}

export default Footer;